body {
  margin: 0;
  line-height: normal;
}

:root {
  /* fonts */
  --font-poppins: Poppins;
  --font-open-sans-hebrew: "Open Sans Hebrew";
  --font-montserrat: Montserrat;

  /* font sizes */
  --font-size-sm: 14px;
  --font-size-base: 16px;
  --font-size-5xl: 24px;
  --font-size-lgi: 19px;
  --font-size-37xl: 56px;
  --font-size-15xl: 34px;
  --font-size-26xl: 45px;
  --font-size-xl: 20px;

  /* Colors */
  --color-white: #fff;
  --white: #fbfbfb;
  --text-and-main-white: #fafafa;
  --color-gray-100: #1f2937;
  --color-deepskyblue-100: #009edf;
  --color-black: #000;

  /* Gaps */
  --gap-base: 16px;
  --gap-5xs: 8px;
  --gap-xl: 20px;

  /* Paddings */
  --padding-21xl: 40px;
  --padding-xl: 20px;
  --padding-111xl: 130px;
  --padding-71xl: 90px;
  --padding-101xl: 120px;
  --padding-59xl: 78px;
  --padding-14xl: 33px;
  --padding-32xl: 51px;
  --padding-20xl: 39px;
  --padding-36xl: 55px;
  --padding-29xl-5: 48.5px;
  --padding-13xl: 32px;
  --padding-5xs: 8px;
  --padding-26xl: 45px;
  --padding-sm: 14px;

  /* Border radiuses */
  --br-xl: 20px;
  --br-31xl: 50px;
}
